<template>
<el-container :gutter="0">
  <div class="warp">
    <ul class="sImg_list">
      <li :class="{active:index==nowIndex}" v-for="(img,index) in smImg" @mouseover="tabShow(index)"><img :src="img.src" :alt="img.alt"/></li>
    </ul>
    <div class="product_show"  id="bigbox">
      <div class="img"  @mousemove="showDetail" id="img" >
        <img :src="bigImg[nowIndex].src" alt="bigImg[nowIndex].alt"  />
      </div>
      <div  class="mark" v-show="isShow" id="mark"  @mouseout="out" ></div>
      <div class="product_detail"  id="detail"></div>

    </div>
  </div>
</el-container>
</template>

<script type="text/ecmascript-6">
    export default{
        data(){
            return {
              posX:"",
              posY:"",
              isShow:false,
              nowIndex:2,
              smImg:[
                {id:1,src:require("../../../assets/images/productDetail/smallImg/smallImg01.jpg"),alt:""},
                {id:2,src:require("../../../assets/images/productDetail/smallImg/smallImg02.jpg"),alt:""},
                {id:3,src:require("../../../assets/images/productDetail/smallImg/smallImg03.jpg"),alt:""},
                {id:4,src:require("../../../assets/images/productDetail/smallImg/smallImg04.jpg"),alt:""},
                {id:5,src:require("../../../assets/images/productDetail/smallImg/smallImg05.jpg"),alt:""}
              ],
              bigImg:[
                {id:1,src:require("../../../assets/images/productDetail/bigImg/bigImg01.jpg"),alt:""},
                {id:2,src:require("../../../assets/images/productDetail/bigImg/bigImg02.jpg"),alt:""},
                {id:3,src:require("../../../assets/images/productDetail/bigImg/bigImg03.jpg"),alt:""},
                {id:4,src:require("../../../assets/images/productDetail/bigImg/bigImg04.jpg"),alt:""},
                {id:5,src:require("../../../assets/images/productDetail/bigImg/bigImg05.jpg"),alt:""}
              ]
            }
        },
    methods:{
      tabShow(index){
        this.nowIndex=index;
      },
      showDetail($event){

        this.call($event)
      },
      call($event){
        this.isShow=true;
        let ev=$event;
        let self=this;
        this.getOffset(ev);

        document.onmousemove=function(ev){
          self.getOffset(ev);
          console.log(bigbox.offsetLeft+","+img.offsetWidth+","+mark.offsetLeft)
          return false;
        }
      },
      out($event){
        this.isShow=false;
      },
      getOffset(ev){
        let bigbox=document.getElementById("bigbox");
        let img=document.getElementById("img");
        let mark=document.getElementById("mark");
        let oTop=bigbox.getBoundingClientRect().top;
        let l=img.offsetWidth-mark.offsetWidth+img.offsetLeft;
        let p=bigbox.offsetHeight-mark.offsetHeight-img.offsetTop;
        let prosX=ev.clientX-bigbox.offsetLeft-img.offsetLeft-210/2;
        let prosY=ev.clientY-oTop-img.offsetTop-210/2;
        this.posX=prosX;
        this.posY=prosY;
        if(prosX>=l){
          prosX=l;
        }else if(prosX<=img.offsetLeft){
          prosX=img.offsetLeft;
        }
        if(prosY>=p){
          prosY=p;
        }else if(prosY<=img.offsetTop){
          prosY=img.offsetTop
        }
        mark.style.left=prosX+"px";
        mark.style.top=prosY+"px";
        console.log(prosX+","+mark.offsetWidth/2)
      },
      setSize(){
      }
    },
    computed:{

    },
    mounted(){

    }
    }
</script>


<style>
.sImg_list{
  width: 64px;
  height: 420px;
  float: left;
}
  .sImg_list li{
    padding: 7px;
    width: 64px;
    height: 64px;
    box-sizing: border-box;
    border: 1px solid #ffffff;
  }
  .sImg_list li:hover, .sImg_list .active{
    border: 1px solid orange;
  }
  .sImg_list img{
    width: 50px;
    height: 50px;
  }
  .product_show{
    float: left;
    position: relative;
    width: 420px;
    height: 420px;
    padding: 7px;

  }
  .product_show span{
    margin-top: 20px;;
  }
  .mark{
    position: absolute;
    left: 7px;
    top: 7px;
    width: 210px;
    height: 210px;
    background: darkcyan;
    opacity: .3;
    z-index: 2;
    overflow: hidden;
  }
  .product_detail{
    width: 800px;
    height:800px;
    background: forestgreen;
    position: absolute;
    left: 100%;
    top: 7px;
    z-index: 5;
    display: none;
  }
</style>



